import {getAuth} from "./auth_reducer";

export const SET_INITIALIZE = "SET_INITIALIZE"


let initialState = {
    isInitialized: false
}


export const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_INITIALIZE:
            return {
                ...state,
                isInitialized: true
            }

        default:
            return state;
    }
}

export const initializeSuccess = () => {
    return {
        type: SET_INITIALIZE
    }
}

export const setInitializeApp = () => async (dispatch) => {
    await dispatch(getAuth())
    await dispatch(initializeSuccess())

    //  
    //const promise = dispatch(getAuth())
    //Promise.all([promise]).then(() => dispatch(initializeSuccess()))
}

