import './App.css';
import './components/HeaderWelcome/HeaderWelcome.module.css'
import './components/Nav/Nav.css'
import './components/Header/Header.module.css'
import './components/Mainbody/MainBody.module.css'
import './components/Dialogs/Dialogs.module.css'
import Header from "./components/Header/Header";
import Nav from "./components/Nav/Nav";
import {BrowserRouter, Route, Routes} from "react-router-dom";
import HeaderWelcomeContainer from "./components/HeaderWelcome/HeaderWelcomeContainer";
import {Component, lazy, Suspense} from "react";
import {connect} from "react-redux";
import {Preloader} from "./components/Common/Preloader/Preloader";
import {setInitializeApp} from "./redux/app_reducer";
import {Skeleton} from "antd";

const MainBodyContainer = lazy(() => import('./components/Mainbody/MainBodyContainer'));
const DialogsContainer = lazy(() => import('./components/Dialogs/DialogsContainer'));
const CreatorsContainer = lazy(() => import('./components/Creators/CreatorsContainer'));
const UsersContainer = lazy(() => import('./components/Users/UsersContainer'));
const LoginContainer = lazy(() => import('./components/Login/Login'));

class App extends Component {
    componentDidMount() {
        this.props.setInitializeApp()
    }

    render() {
        if (!this.props.isInitialized) {
            return (
                <Preloader/>
            )
        }

        return (
            <BrowserRouter basename={process.env.PUBLIC_URL}>
                <div className={"app-wrapper"}>
                    <Header/>
                    <HeaderWelcomeContainer/>
                    <Nav/>
                    <div>
                        <Suspense fallback={<Skeleton active/>}>
                            <Routes>
                                <Route path={"/MainBody/:userId?"} element={<MainBodyContainer/>}/>
                                <Route path={"/Dialogs"} element={<DialogsContainer/>}/>
                                <Route path={"/Creators"} element={<CreatorsContainer/>}/>
                                <Route path={"/Users"} element={<UsersContainer/>}/>
                                <Route path={"/Login"} element={<LoginContainer/>}/>
                            </Routes>
                        </Suspense>
                    </div>
                </div>
            </BrowserRouter>
        );
    }
}

export const mapStateToProps = (state) => {
    return {
        isInitialized: state.app.isInitialized
    }
}

export default connect(mapStateToProps, {setInitializeApp})(App);
