import React from 'react'
import HeaderWelcome from "./HeaderWelcome";
import {connect} from "react-redux";
import {loginOut, setAuthUserData} from "../../redux/auth_reducer";

class HeaderWelcomeContainer extends React.Component {

    render() {
        return <HeaderWelcome {...this.props} />
    }
}

const mapStateToProps = (state) => {
    return {
        isAuth: state.authUser.isAuth,
        login: state.authUser.login
    }
}

export default connect(mapStateToProps, {setAuthUserData, loginOut})(HeaderWelcomeContainer);