import axios from "axios";

const instance = axios.create({
    withCredentials: true,
    headers: {
        "api-key": "d22a310b-4ba2-402e-8895-4dcc3d76b2ca"
    },
    baseURL: "https://social-network.samuraijs.com/api/1.0/"
})


export const AuthAPI = {
    getAuth: () => {
        return (
            instance.get(`auth/me`))
    },

    loginAuth: (email, password, rememberMe = false) => {
        return (
            instance.post(`auth/login`, {email, password, rememberMe})
        )
    },

    loginOut: () => {
        return (
            instance.delete(`auth/login`)
        )
    }
}


export const UsersAPI = {
    getUsers: (currentPage = 1, pageSize = 10) => {
        return (
            instance.get(`users?page=${currentPage}&count=${pageSize}`).then(response => response.data))
    },

    unfollowUser: (userId) => {
        return (
            instance.delete(`follow/${userId}`).then(response => response))
    },

    followUser: (userId) => {
        return (
            instance.post(`follow/${userId}`).then(response => response))
    },

    getProfile: (userId) => { //This method is outdated
        return (
            ProfileAPI.getProfile(userId)
        )
    }
}

export const ProfileAPI = {
    getProfile: (userId) => { //Use this method instead of UsersAPI.getProfile
        return (
            instance.get(`profile/${userId}`)
        )
    },
    getStatus: (userId) => {
        return (
            instance.get(`profile/status/${userId}`)
        )
    },
    updateStatus: (status) => {
        return (
            instance.put(`profile/status`, {status})
        )
    }
}

