import {NavLink} from "react-router-dom";
import s from "./HeaderWelcome.module.css"

function HeaderWelcomeForm(props) {
    return (
        <div className={s.backHead}>
            <div className={s.headerWelcome}>
                <a>Icarus</a>
            </div>
            <div className={s.loginBlock}>
                {props.isAuth ?
                    <div> {props.login} <br/><button onClick={props.loginOut}>Logout</button>
                    </div> :
                    <NavLink to={"/login"}>Login</NavLink>}
            </div>
        </div>
    )
}

export default HeaderWelcomeForm;