import {NavLink} from "react-router-dom";

function Nav() {
    return (
        <div className={"Nav"}>
            <div>
                <NavLink to={"/MainBody"}>Profile</NavLink>
            </div>

            <div>
                <NavLink to={"/Dialogs"}>Dialogs</NavLink>
            </div>

            <div>
                <NavLink to={"/Creators"}>Creators</NavLink>
            </div>

            <div>
                <NavLink to={"/Users"}>Users</NavLink>
            </div>
        </div>
    )
}

export default Nav;