export const addCreator = "addCreator";
export const updateCreator = "updateCreator";


let initialState = {
    creatorName: "Name",
    creatorData: [
        {name: "Jax", articles: "4"},
        {name: "Lane", articles: "2"},
        {name: "Ivory", articles: "7"}
    ]
}


export const creatorReducer = (state = initialState, action) => {
    switch (action.type) {
        case addCreator: {
            return {
                ...state,
                creatorData: [...state.creatorData, {name: state.creatorName, articles: "1"}]
            };
        }

        case updateCreator: {
            return {
                ...state,
                creatorName: action.newNameCreator
            };
        }

        default:
            return state;
    }
}


export let addCreatorActionCreator = () => {
    return {
        type: addCreator
    }
}

export let updateCreatorActionCreator = (text) => {
    return {
        type: updateCreator,
        newNameCreator: text
    }
}

