export const sendMessage = "sendMessage";


let initialState = {
    dialogsData : [
        {name: "John", id: 1},
        {name: "Mariel", id: 2},
        {name: "Professor", id:3}
    ],
    messagesData : [
        {message: "How are you", id: 1},
        {message: "What's up", id: 2},
        {message: "I need your report today", id:3}
    ],
    mess: " "
}


export const dialogReducer = (state = initialState, action) => {
    switch(action.type) {
        case sendMessage:
            //let newMessage = action.newMessageBody;
            return {
                ...state,
                messagesData: [...state.messagesData, {message: action.newMessageBody, id: 4}]
            }

        default:
            return state;
    }
}

export let sendUserMessage = (newMessageBody) => {
    return {
        type: sendMessage, newMessageBody
    }
}
