import {AuthAPI} from "../api/api";
import {stopSubmit} from "redux-form";

export const SET_AUTH_USER_DATA = "SET_AUTH_USER_DATA"


let initialState = {
    userId: 2,
    login: null,
    email: null,
    isAuth: false
}


export const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_AUTH_USER_DATA:
            return {
                ...state,
                ...action.data
            }

        default:
            return state;
    }
}

export let setAuthUserData = (userId, login, email, isAuth) => {
    return {
        type: SET_AUTH_USER_DATA, data: {userId, login, email, isAuth}
    }
}

export const getAuth = () => async (dispatch) => {
    let response = await AuthAPI.getAuth();

    if (response.data.resultCode === 0) {
        let {id, login, email} = response.data.data
        dispatch(setAuthUserData(id, email, login, true))
    }
}


export const loginAuth = (email, password, rememberMe) => async (dispatch) => {
    let response = await AuthAPI.loginAuth(email, password, rememberMe);

    if (response.data.resultCode === 0) {
        dispatch(getAuth())
    } else {
        let someErr = response.data.messages.length > 0 ? response.data.messages[0] : "some error";
        dispatch(stopSubmit("login",
            {_error: someErr}));
    }
}


export const loginOut = () => async (dispatch) => {
    await AuthAPI.loginOut();
    await dispatch(setAuthUserData(null, null, null, false))
}




