import {UsersAPI} from "../api/api";

export const USER_FOLLOW = "userFollow"
export const USER_UNFOLLOW = "userUnfollow"
export const SET_USERS = "setUsers"
export const SET_CURRENT_PAGE = "setCurrentPage"
export const SET_TOTAL_USER_COUNT = "setTotalUserCount"
export const SET_IS_FETCHING = "setIsFetching"
export const FOLLOWING_IN_PROGRESS = "followingInProgress"

let initialState = {
    users: [],
    pageSize: 4,
    totalUserCount: 0,
    currentPage: 1,
    isFetching: false,
    followingInProgress: [],
    portionSize: 0
}

export const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case USER_FOLLOW:
            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.userId) {
                        return {...u, followed: true}
                    }

                    return u;
                })
            }

        case USER_UNFOLLOW:
            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.userId) {
                        return {...u, followed: false}
                    }

                    return u;
                })
            }

        case SET_USERS: {
            return {...state, users: action.users}
        }

        case SET_CURRENT_PAGE: {
            return {...state, currentPage: action.page}
        }

        case SET_TOTAL_USER_COUNT: {
            return {...state, totalUserCount: action.count}
        }

        case SET_IS_FETCHING: {
            return {...state, isFetching: action.isFetching}
        }

        case FOLLOWING_IN_PROGRESS: {
            return {
                ...state, followingInProgress: action.following
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id !== action.userId)
            }
        }

        default:
            return state;
    }
}

export let followUser = (userId) => {
    return {
        type: USER_FOLLOW, userId
    }
}

export let unfollowUser = (userId) => {
    return {
        type: USER_UNFOLLOW, userId
    }
}

export let setUsers = (users) => {
    return {
        type: SET_USERS, users
    }
}

export let setCurrentPage = (page) => {
    return {
        type: SET_CURRENT_PAGE, page
    }
}

export let setTotalUserCount = (count) => {
    return {
        type: SET_TOTAL_USER_COUNT, count
    }
}

export let setIsFetching = (isFetching) => {
    return {
        type: SET_IS_FETCHING, isFetching
    }
}

export let toggleFollowingProgress = (following, userId) => {
    return {
        type: FOLLOWING_IN_PROGRESS, following, userId
    }
}

let workWithFollowing = async (userId, dispatch, apiMethod, actionCreator) => {
    await dispatch(toggleFollowingProgress(true, userId))
    let response = await apiMethod(userId);

    if (response.data.resultCode === 0) {
        await dispatch(actionCreator)
    }
    await dispatch(toggleFollowingProgress(false, userId))
}

export const getUsers = (currentPage, pageSize) => async (dispatch) => {
    await dispatch(setIsFetching(true))
    let data = await UsersAPI.getUsers(currentPage, pageSize)
    await dispatch(setIsFetching(false))
    await dispatch(setUsers(data.items))
    await dispatch(setTotalUserCount(data.totalCount))
}


export const follow = (userId) => {
    return async (dispatch) => {
        let actionCreator = followUser(userId);
        let apiMethod = await UsersAPI.followUser.bind(UsersAPI);

        await workWithFollowing(userId, dispatch, apiMethod, actionCreator)
    } 
}


export const unfollow = (userId) => {
    return async (dispatch) => {
        let actionCreator = unfollowUser(userId);
        let apiMethod = await UsersAPI.unfollowUser.bind(UsersAPI);

        await workWithFollowing(userId, dispatch, apiMethod, actionCreator)
    }
}

