import {ProfileAPI} from "../api/api";

export const addPost = "addPost";
export const SET_USER_PROFILE = "SET_USER_PROFILE";
export const GET_USER_STATUS = "GET_USER_STATUS";

let initialState = {
    post: [
        {message: "Hello everybody", number: 4},
        {message: "There are a lot of houses", number: 11}
    ],
    profile: null,
    status: ""
}

export const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case addPost: {
            return {
                ...state,
                post: [...state.post, {message: action.postText, number: 3}]
            };
        }

        case SET_USER_PROFILE: {
            return {
                ...state,
                profile: action.profile
            }
        }

        case GET_USER_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }

        default:
            return state;
    }
}

export let addProfilePost = (postText) => {
    return {
        type: addPost, postText
    }
}

export let setUserProfile = (profile) => {
    return {
        type: SET_USER_PROFILE, profile
    }
}

export let getUserStatus = (status) => {
    return {
        type: GET_USER_STATUS, status
    }
}

export const getProfile = (userId) => async (dispatch) => {
    let response = await ProfileAPI.getProfile(userId)
    await dispatch(setUserProfile(response.data))
}


export const getStatus = (userId) => async (dispatch) => {
    let response = await ProfileAPI.getStatus(userId)
    await dispatch(getUserStatus(response.data))
}


export const updateStatus = (status) => async (dispatch) => {
    let response = await ProfileAPI.updateStatus(status)

    if (response.data.resultCode === 0) {
        await dispatch(getUserStatus(status))
    }
}



